$('.js-pdf-popup')
	.each((index, component) => {
		let $component = $(component);
		let $body = $(document.body);
		let $closeBtn = $component.find('.js-popup-close');

		$(document.body)
			.on('pdf:show', () => {
				$body.stop(true, true).addClass('show-popup');
				$component.fadeIn(200);
			});

		$(document.body)
			.on('pdf:hide', () => {
				$body.stop(true, true).removeClass('show-popup');
				$component.fadeOut(200);
			});

		$closeBtn.on('click', (e) => {
			e.preventDefault();

			$(document.body)
				.trigger('pdf:hide');
		});
	});

$('.js-material-item-document')
	.on('click', (e) => {
		e.preventDefault();

		let $trgt = $(e.currentTarget);
		let embedStr = `<embed src="${$trgt.attr('href')}" type="application/pdf" width="100%" height="100%"/>`;

		$('.js-pdf-popup')
			.find('.js-embed')
			.html(embedStr);
		$(document.body)
			.trigger('pdf:show');
	});
