$('.js-scroller')
	.each((index, component) => {
		let $component = $(component);
		let toTopBtn = $('.js-to-top-btn');

		$(window).on('scroll', () => {
			if ($(window).scrollTop() >= $(window).innerHeight()) {
				toTopBtn.addClass('to-top-btn_active');
			} else {
				toTopBtn.removeClass('to-top-btn_active');
			}
		});

		toTopBtn.on('click', (e) => {
			e.preventDefault();

			$(window).scrollTop(0);
		});
	});
